	module.exports = {"data":{"talkJson":{"description":"\"Speaker: Ryan Kelly\n\nPyPy.js is an experiment in building a fast and compliant in-browser python interpreter, by compiling PyPy into javascript and retargeting its JIT to emit javascript code at runtime. This talk will demonstrate the combination of technologies that make such a thing possible, the results achieved so far, and the challenges that still remain when taking python onto javascript's home turf.\n\nSlides can be found at: https://speakerdeck.com/pycon2015 and https://github.com/PyCon/2015-slides\"","meta":{"duration":"PT30M13S"},"presenter":{"name":null},"slug":"ryan-kelly-pypyjs-what-how-why-pycon-2015","source":{"id":"PiBfOFqDIAI","provider":"youtube"},"tag":[],"title":"Ryan Kelly - PyPy.js: What? How? Why? - PyCon 2015"},"allTalkJson":null},"pathContext":{"slug":"ryan-kelly-pypyjs-what-how-why-pycon-2015","tags":[]}}

